#container{
  background: url(/static/media/frontpagebg.acf95bf1.jpg) no-repeat;
  width: 100%;
  height: 100%;
}

#container::before{
  content: "";
  background-color: rgba(0,0,0,0.6);
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

#container .logo-container{
  margin-top: 0.1rem;
  position: absolute;
  color: #fff;
  z-index: 100;
  margin-left: 0.2rem;
  height: 50px;
}
#container .logo-container img{
  width:0.8rem;
  vertical-align: middle;
}
#container .admin{
  top: 0.2rem;
  position: absolute;
  color: #fff;
  z-index: 100;
  height: 50px;
  right: 0.5rem;
  width: 0.8rem;
  display: block;
  height: 0.8rem;
}
#container .admin:hover{
  cursor: pointer;
}
#container .logo-container span{
  font-size: 0.5rem;
  font-weight: 600;
  padding-left: 0.2rem;
  display: inline-block;
  line-height: 50px;
  vertical-align: middle;
}
#container .inner-container{
  position: absolute;
  color: #fff;
  text-align: center;
  font-size: 0.7rem;
  z-index: 100;
  left:50%;
  top: 45%;
  -webkit-transform: translate(-50%,0);
          transform: translate(-50%,0);
}
#container .inner-container p{
  padding-bottom: 0.2rem;
  margin-bottom: 0;
}
#container .search{
  width: 2.5rem;
  height: 0.8rem;
  font-size: 0.4rem;
}

#secondContainer{
  display: grid;
  height: 100%;
  grid-template-columns:1fr 1fr 1fr;
}

#secondContainer::after{
  content: "";
  background-color: rgba(0,0,0,0.6);
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

#secondContainer .bgbox{
  position: relative;
}
#secondContainer .bg1box{
  background: url(/static/media/bg1.ce6f3bcf.jpg);
  background-size: cover;
}

#secondContainer .bg2box{
  background: url(/static/media/bg2.a40863d7.jpg);
  background-size: cover;
}

#secondContainer .bg3box{
  background: url(/static/media/bg3.c3a9f400.jpeg);
  background-size: cover;
}

#secondContainer .subTitle{
  z-index: 100;
  position: absolute;
  left: 50%;
  top: 30%;
  -webkit-transform: translate(-50%,0);
          transform: translate(-50%,0);
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

#secondContainer .subTitle img{
  width: 3.5rem;
  display: block;
  margin-bottom: 0.2rem;
}
#secondContainer .subTitle .btn{
  width: 2.5rem;
  height: 0.8rem;
  font-size: 0.3rem;
}
@media screen and (max-width:768px){
  #secondContainer{
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 1fr 1fr;
  }
  #secondContainer .subTitle{
    top: 15%;
  }
  #secondContainer .subTitle img{
    width: 2rem;
  }
}

